<template>
  <div class="hello">
    <v-image alt="Alt Text for the Image" :name="myImage" v-bind:img-style="style" @load-image="localVariable"></v-image>
  </div>
</template>

<script>
import vImage from 'v-image';

export default {
  components: { vImage },
  methods: {
    localVariable(imageSentFromComponent) {
      this.image = imageSentFromComponent;
    },
  },
  data() {
    return {
      image: '',
      style: {
        height: '200px',
        width: '200px',
      },
    };
  },
};
</script>



